          </div>
          <div>
            <p class="blueText" @click="showIosGuidePopup()">ios安装指引</p>
          </div>
          <div>
            <div id="m" class="installBox" style="display: none">
              <a class="down" :href="downLink" download="" @click="onDownClick">免费安装</a>
