      </van-row>
      <!-- ---------- rate 5 ----------- -->
      <van-row style="padding: 30px 0px 10px">
        <van-col span="10">
          <div class="fivestar">5.0 ★★★★★</div>
          <p class="commonText" style="color: #d8d8d8">19k 个评分</p>
        </van-col>
        <van-col span="6">
          <div class="centerBox">
            <i style="position: relative; top: -6px; padding-right: 2px">#</i>3
          </div>
        </van-col>
        <van-col span="8" style="text-align: right">
          <div class="fivestar">18+</div>
