        </van-col>
      </van-row>
      <van-divider />
      <!-- ------------- ratings and review --------- -->
      <van-row>
        <van-row>
          <p class="ratingTopic">评分及评论</p>
        </van-row>
        <van-row>
          <van-col span="8" style="text-align: center">
            <p class="mainRate">4.9</p>
            <div class="mainRateBottom">满分 5 分</div>
          </van-col>
          <van-col span="16">
            <div class="star_row">
              <span class="s1"><i></i></span>
              <div class="lineBox">
                <span class="v1"></span>
              </div>
            </div>
            <div class="star_row">
              <span class="s2"><i></i></span>
              <div class="lineBox">
                <span class="v2"></span>
              </div>
            </div>
            <div class="star_row">
              <span class="s3"><i></i></span>
              <div class="lineBox">
                <span class="v3"></span>
              </div>
            </div>
            <div class="star_row">
              <span class="s4"><i></i></span>
              <div class="lineBox">
                <span class="v4"></span>
              </div>
            </div>
            <div class="star_row">
              <span class="s5"><i></i></span>
              <div class="lineBox">
                <span class="v5"></span>
              </div>
            </div>
            <div>
              <p class="mainRateRightText">19k 个评分</p>
            </div>
          </van-col>
        </van-row>
      </van-row>
      <van-divider />
      <!-- ------------- new function -------------- -->
      <van-row>
        <p class="ratingTopic" style="padding: 0px 0px 10px">新功能</p>
        <p style="margin: 0px 0px; font-size: 0.3rem">{{ version }}</p>
      </van-row>
      <van-divider />
      <!-- ----------- information ------------- -->
      <van-row>
        <p class="ratingTopic" style="padding: 0px">信息</p>
      </van-row>
      <van-row class="infoBox">
        <ul>
          <li>
            <span>大小</span>
            <p>未知</p>
          </li>
          <li>
            <span>兼容性</span>
            <p>
              支持安卓和苹果，iOS需要 14
              或更高版本。ios低版本可能会出现不全屏的情况
            </p>
          </li>
          <li>
            <span>语言</span>
            <p>简体中文</p>
          </li>
          <li>
            <span>年龄分级</span>
            <p>限18岁以上</p>
          </li>
          <li>
            <span>Copyright</span>
            <p>© 2022 MobiFun Games Inc</p>
          </li>
          <li>
            <span>价格</span>
            <p>免费</p>
          </li>
          <li>
            <span>隐私政策</span>
            <p>✋</p>
          </li>
        </ul>
      </van-row>
      <!-- -------------- footer --------------- -->
      <van-row class="footer">
        <p>免责声明：</p>
        <p style="text-indent: 15px">
          本网站仅为开发者提供App的下载和安装托管，App内的内容和运营相关事项由App开发者负责，与本网站无关
        </p>
      </van-row>
    </van-row>
    <!-- ----------- ios installation guide ---------------- -->
    <van-popup v-model="showIosGuide" style="width: 90%; height: 95%; padding: 10px">
      <p class="closeText" @click="hideIosGuidePopup()">关闭</p>
      <van-row>
        <img width="100%" src="../assets/images/ios1.jpg" />
        <p class="popupText">第一步 允许下载APP</p>
        <img width="100%" src="../assets/images/ios2.jpg" />
        <p class="popupText">第二步 允许配置 APP</p>
        <img width="100%" src="../assets/images/ios3.jpg" />
        <p class="popupText">第三步 点击右上角安装按钮</p>
        <img width="100%" src="../assets/images/ios4.jpg" />
      </van-row>
    </van-popup>

    <div id="weixin_ios" style="">
      <div class="click_opacity"></div>
      <div class="to_btn">
        <span class="span1">
          <img src="../assets/images/click_btn.png">
        </span>
        <span class="span2">
          <em>1</em> 点击右上角
          <img src="../assets/images/menu.png">
          打开菜单
        </span>
        <span class="span2">
          <em>2</em> 选择
          <img src="../assets/images/safari.png">
          用Safari打开下载
        </span>

      </div>
    </div>
    <div id="weixin_android" style="display: none">
      <div class="click_opacity"></div>
      <div class="to_btn"><span class="span1">
          <img src="../assets/images/click_btn.png">
        </span>
        <span class="span2">
          <em>1</em> 点击右上角
          <img src="../assets/images/menu_android.png">
          打开菜单
        </span>
        <span class="span2 android_open">
          <em>2</em> 选择
          <img src="../assets/images/android_.png">
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import QRCode from "qrcodejs2";
import JSEncrypt from "jsencrypt/bin/jsencrypt";
export default {
  name: "Download",
  data() {
    return {
      showIosGuide: false,
      title: '',
      logo: '',
      version: '',
      weixin: false,
      weibo: false,
      isQQ: false,
      isiOS: false,
      isAndroid: false,
      isSafari: false,
      downLink: '',
      provisionLink: ''
    };
  },
  methods: {
    showIosGuidePopup() {
      this.showIosGuide = true;
    },
    hideIosGuidePopup() {
      this.showIosGuide = false;
    },
    qrcode(url) {
      let qrcode = new QRCode("qrcode", {
        text: url, // 二维码地址
      });
    },
    showPopup() {
      let that = this;
      var browser = {
        versions: function () {
          var u = navigator.userAgent;
          var ua = navigator.userAgent.toLocaleLowerCase();
          var app = navigator.appVersion;
          return {
            trident: u.indexOf('Trident') > -1, // IE内核
            presto: u.indexOf('Presto') > -1, // opera内核
            webKit: u.indexOf('AppleWebKit') > -1, //苹果、谷歌内核
            gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1, // 火狐内核
            mobile: !!u.match(/AppleWebKit.*Mobile.*/) || !!u.match(/AppleWebKit/), // 是否为移动终端
            ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), // IOS终端
            android: u.indexOf('Android') > -1, // 安卓终端
            iPhone: u.indexOf('iPhone') > -1 || u.indexOf('Mac') > -1, // 是否为iphone或QQHD浏览器
            iPad: u.indexOf('iPad') > -1, // 是否为iPad
            webApp: u.indexOf('Safari') == -1, // 是否web应用程序，没有头部与底部
            QQbrw: u.indexOf('MQQBrowser') > -1, // QQ浏览器
            weiXin: u.indexOf('MicroMessenger') > -1, // 微信
            QQ: ua.match(/\sQQ/i) == " qq", // QQ App内置浏览器（需要配合使用）
            weiBo: ua.match(/WeiBo/i) == "weibo", // 微博
            safari: /Safari/.test(u) && !/Chrome/.test(u) && u.indexOf('MQQBrowser') < 0 && !/CriOS/.test(u),
            UCbrw: u.indexOf('UCBrowser') > -1, //UC浏览器
            ucSpecial: u.indexOf('rv:1.2.3.4') > -1,
            Symbian: u.indexOf('Symbian') > -1,
            ucSB: u.indexOf('Firofox/1.') > -1
          };
        }(),
        language: (navigator.browserLanguage || navigator.language).toLowerCase()
      };
      var ua = navigator.userAgent.toLowerCase();//获取判断用的对象
      if (browser.versions.mobile) {//判断是否是移动设备打开
        if (browser.versions.weiXin) {
          that.weixin = true;
        }
        if (browser.versions.weiBo) {
          that.weibo = true;
        }
        if (browser.versions.QQ) {
          that.isQQ = true;
        }

        if (browser.versions.ios || browser.versions.iPhone || browser.versions.iPad) {
          that.isiOS = true;
        }
        if (browser.versions.android) {
          that.isAndroid = true;
        }
        if (browser.versions.safari) {
          that.isSafari = true;
        } else {
          that.isSafari = false;
        }

      }

      // var appType = (this.data.ext == 'iOS') ? 'ios' : 'android';
      if (that.weixin == true) {
        $('.down_load').hide();
        if (that.isiOS == true) {
          $("#weixin_ios").show();
          $("#weixin_android").hide();
        } else {
          $("#weixin_ios").hide();
          $("#weixin_android").show();
        }
      } else {
        $("#weixin_ios").hide();
        $("#weixin_android").hide();
      }
      if (that.isQQ) {
        $('.down_load').hide();
        if (that.isiOS == true) {
          $("#weixin_ios").show();
          $("#weixin_android").hide();
        } else {
          $("#weixin_ios").hide();
          $("#weixin_android").show();
        }
        return false;
      }

      if (that.isiOS && !that.isSafari && (browser.versions.UCbrw)) {
        $('.down_load').hide();
        $("#no_safari").show();
      }
    },
    onDownClick() {
      // this.download(this.downLink);

      var ua = window.navigator.userAgent.toLowerCase();
      var Sys = {};
      var s;
      (s = ua.match(/version\/([\d.]+).*safari/)) ? (Sys.safari = s[1]) : 0;
      if (/(iPhone|iPad|iPod|iOS)/i.test(ua)) {
        setTimeout(() => {
          this.download(this.provisionLink)
        }, 3000);
      }
    },
    download(url) {
      var a = document.createElement('a');
      a.setAttribute('href', url);
      a.setAttribute('download', '');
      a.click();
    }
  },
  mounted: function () {
    this.showPopup();
    this.qrcode(window.location.href);
    // -------- check device is pc or mobile ----------
    if (!/windows phone|iphone|android/gi.test(window.navigator.userAgent)) {
      document.getElementById("pc").style.display = "block";
    } else {
      document.getElementById("m").style.display = "block";
    }
    // ----------- check android or apple device ----------
    var ua = window.navigator.userAgent.toLowerCase();
    var Sys = {};
    var s;
    (s = ua.match(/version\/([\d.]+).*safari/)) ? (Sys.safari = s[1]) : 0;
    if (/(iPhone|iPad|iPod|iOS)/i.test(ua)) {
      // $(".down").click(function (event) {
      //   $(".down").attr("href", window.__env.iosDownloadLink);
      // });
      this.downLink = window.__env.iosDownloadLink;
      this.provisionLink = window.__env.iosMobileProvision;
    }
    if (/(Android)/i.test(ua)) {
      // $(".down").attr("href", window.__env.androidDownloadLink);
      this.downLink = window.__env.androidDownloadLink;
    }

    this.title = window.__env.title;
    this.logo = window.__env.logo;
    this.version = window.__env.version;
  },
};
</script>

<style scoped>
.main {
  padding: 15px;
}

.strong {
  color: #111;
  font-size: 0.4rem;
  display: block;
  margin: 0;
  font-weight: 700;
}

.strong span {
  color: #8e8e93;
  font-size: 0.24rem;
  border-radius: 0.08rem;
  padding: 0.04rem 0.1rem;
  border: 1px solid #8e8e93;
  vertical-align: middle;
  margin-left: 0.1rem;
}

.alignContent {
  align-items: center;
  display: flex;
}

.agency_qrcode {
  width: 100%;
  padding: 10px 0px 0px 60px;
}

.qrText {
  color: #333333;
  font-size: 0.28rem;
  margin: 0.1rem 0px;
}

