  margin: 0px;
  padding: 10px 0px;
}

.mainRate {
  font-size: 1.2rem;
  color: #4c4c50;
  margin: 0px;
  font-weight: 700;
}

.mainRateBottom {
  font-size: 0.28rem;
  top: -6px;
  position: relative;
  color: #8e8e93;
  font-weight: 700;
}

/* ------- 5 star section ------ */
.star_row {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.star_row span {
  width: 1.2rem;
  height: 0.24rem;
  display: inline-block;
  text-align: right;
  position: relative;
}

.star_row span i {
  height: 0.16rem;
  display: inline-block;
  background: url(../assets/images/star.svg) 0 0;
  background-size: 0.18rem 0.34rem;
  position: absolute;
  right: -12%;
  top: 12%;
}

.star_row span.s1 i {
  width: 0.9rem;
}

.star_row .lineBox {
  width: calc(100% - 1.6rem);
  height: 0.05rem;
  border-radius: 0.3rem;
  background-color: #e5e5e5;
  overflow: hidden;
}

.star_row .lineBox span {
  height: 100%;
  border-radius: 0 3px 3px 0;
  background-color: #8e8e93;
  float: left;
}

.star_row .lineBox span.v1 {
  width: 90%;
}

/* ----- 4 star ---- */
.star_row span.s2 i {
  width: 0.72rem;
}

.star_row .lineBox span.v2 {
  width: 10%;
}

/* ----- 3 star ---- */
.star_row span.s3 i {
  width: 0.54rem;
}

.star_row .lineBox span.v3 {
  width: 4%;
}

/* ----- 2 star ---- */
.star_row span.s4 i {
  width: 0.36rem;
}

.star_row .lineBox span.v4 {
  width: 3%;
}

/* ----- 1 star ---- */
.star_row span.s5 i {
  width: 0.18rem;
}

.star_row .lineBox span.v5 {
  width: 1%;
}

.mainRateRightText {
  font-size: 0.28rem;
  position: relative;
  color: #8e8e93;
  margin: 0px;
  text-align: right;
}

/* ------- info box ---------- */
.infoBox ul li {
  line-height: 1.5;
  padding: 0.15rem 0;
  float: none;
  border-bottom: 1px solid #ebedf0;
  display: flex;
  justify-content: space-between;
}

.infoBox ul li span {
  font-size: 0.24rem;
  color: #8e8e93;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.infoBox ul li p {
  width: 80%;
  color: #333;
  font-size: 0.24rem;
  text-align: right;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  word-wrap: break-word;
  margin: 0;
}

.footer {
  background-color: #eee;
  color: #a9a9a9;
  padding: 10px;
}

.footer p {
  margin: 0;
  font-size: 0.2rem;
}

/* ----------- popup ----------- */
.closeText {
  margin: 0;
  padding: 0px 0px 10px;
  font-size: 14px;
  font-weight: 700;
  text-align: right;
}

.popupText {
  margin: 0;
  padding: 10px 0px;
  font-size: 14px;
  font-weight: 700;
}

/* For Desktop View */
@media screen and (min-width: 1024px) {
  .main {
    width: 750px;
    margin-left: -375px;
    left: 50%;
    position: relative;
  }
}

/* For Tablet View */
@media screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .main {
    padding: 15px;
    width: 750px;
    margin-left: -390px;
    left: 50%;
    position: relative;
  }
}

.click_opacity {
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.6;
  position: fixed;
  z-index: 10000;
  top: 0px;
}

.to_btn {
  position: fixed;
  top: 10px;
  right: 10px;
  text-align: right;
  z-index: 10001;
  font-family: "微软雅黑";
}

.to_btn span {
  display: block;
}

.to_btn img {
  width: 20%;
  height: auto;
  display: inline-block;
}

.to_btn .span1 {
  font-size: 1.6rem;
  color: #fff;
  margin-top: 5px;
}

.to_btn {
  color: #fff;
}

.to_btn .span2 {
  display: inline-block;
  line-height: 36px;
  width: 80%;
  margin-bottom: 12px;
  text-align: left;
  font-size: 16px;
}

.to_btn .span2 em {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: #009dd9;
  color: #fff;
  font-size: 12px;
  text-align: center;
  line-height: 16px;
  border: 1px solid #fff;
  border-radius: 50%;
  margin-right: 3px;
}

.to_btn .span2 img {
  display: inline-block;
  width: 30px;
  height: 30px;
  margin: 0px 5px;
}

.to_btn span {
  display: block;
  float: right;
}

.to_btn .android_open img {
  display: inline-block;
  width: 150px;
  height: 34px;
}
</style>



